import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from'react-router-dom'

export default function Navbar(props) {
  return (
    <nav className={`navbar navbar-expand-lg navbar-${props.togglenav}} bg-${props.mode}`}>
      <div className="container-fluid">
        <a className="navbar-brand" href="/">
        <img src="./ms-icon-70x70.png" width="30" height="30" className="d-inline-block align-top" alt="Txtutils"/>
        {" " +props.title}
          </a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
              <a className="nav-link active" aria-current="page" href="/">Home</a>
            </li>
            <li className="nav-item">
              <a className="nav-link active" aria-current="page" href="/">{props.heading2}</a>
            </li>
          </ul>
          <form className="d-flex" role="search">
            <input className="form-control me-2 " type="search" placeholder="Search" aria-label="Search"/>
            <button className="btn btn-outline-info" type="submit">Search</button>
          </form>
          <div className="btn-group mx-2" role="group" aria-label="Basic mixed styles example">
            <button type="button" className="btn btn-danger" onClick={props.toggleRed}>Red</button>
            <button type="button" className="btn btn-success" onClick={props.toggleGreen}>Green</button>
            {/* <button type="button" className="btn btn-primary" onClick={props.toggleBlue}>Blue</button> */}
          </div>
          <div className={`form-check form-switch text-${props.mode==='light'?'dark':'light'}`}>
          <input className="form-check-input mx-2" type="checkbox" onClick={props.toggleMode} role="switch" aria-checked id="flexSwitchCheckDefault"/>
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Enable Dark Mode</label>
        </div>
        </div>
      </div>
    </nav>
  )
}
Navbar.propTypes={title: PropTypes.string.isRequired,
                    heading2: PropTypes.string.isRequired
                }
Navbar.defaultProps = {
                    title: 'Insert Title Here',
                    heading2: 'Insert 2nd Title Here'
                  };