import React,{useState} from 'react'

export default function TextForm(props) {
    const Uppfun=()=>{
    
        let newText=text.toUpperCase();
        setText(newText);
        props.showAlert("Converted to Uppercase!","success");
    }
    const Lofun=()=>{
  
      let newText=text.toLowerCase();
      setText(newText);
      props.showAlert("Converted to Lowercase!","success");
  }
  const ClearText=()=>{

    let newText='';
    setText(newText);
    props.showAlert("Cleared Text","success");
}
    const CopyText=()=>{
      navigator.clipboard.writeText(text);
      props.showAlert("Copied Text","success");
    }
const handleReverseClick = () => {
  let reverseText=text.split("")
  reverseText=reverseText.reverse();
  reverseText=reverseText.join('');
  reverseText=reverseText.replace(",","");
  setText(reverseText);
  props.showAlert("Reversed Text","success");
}
    const chng=(event)=>{
        //console.log("On Change")
        setText(event.target.value);
    }
    const [text,setText]=useState('');
  return (
    <>
    <div className="container my-2" style={{color: props.mode==='dark'?'white':'#01011c'}}>
      <h1>{props.heading}</h1>
      <div>
        <textarea id="TextArea" className="form-control" style={{backgroundColor: props.mode==='dark'?'grey':'white', color:props.mode==='dark'?'white':'black' }} onChange={chng} value={text} rows="8" />
      </div>
      <button disabled={text.length===0} className="btn btn-primary my-1 mx-1 " onClick={Uppfun} >Convert to Uppercase</button>
      <button disabled={text.length===0} className="btn btn-info my-1 mx-1" onClick={Lofun} >Convert to Lowercase</button>
      <button disabled={text.length===0} className="btn btn-warning my-1 mx-1" onClick={ClearText}>Clear Text</button>
      <button disabled={text.length===0} className="btn btn-danger my-1 mx-1" onClick={handleReverseClick}>Reverse the String</button>
      <button disabled={text.length===0} className="btn btn-secondary my-1 mx-1" onClick={CopyText}>Copy Text</button>
    </div>
    <div className="container my-2" style={{color: props.mode==='dark'?'white':'#01011c'}}>
      <h1>Your text summary</h1>
      <p>{text.split(/\s+/).filter((element)=>{return element.length!==0}).length} words and {text.length} characters</p>
      <p>{0.008*(text.split(" ").length-1)} Minutes needed to read this</p>
      <h2>Preview</h2>
      <p>{text.length>0?text:'Enter something in the above textbox to preview it here'}</p>
      <p>{text}</p>
    </div>
    </>
  )
}
