import React, { useState } from 'react';
import './App.css';
import Alert from './Components/Alert';
// import About from './Components/About';
import Navbar from './Components/Navbar';
import TextArea from './Components/TextForm';
// import {
//   BrowserRouter as Router,
//   Routes,
//   Route,
// } from "react-router-dom";

function App() {
  const [mode, setMode] = useState('light'); //whether light mode is enabled or not
  const [alert, setAlert] = useState(null); //set alert
  const [redmode, setRedMode] = useState(null); //setting Red mode
  const [greenmode, setGreenMode] = useState('null'); //setting Green alert
  // const [bluemode, setBlueMode] = useState('null'); //setting Blue alert
  const [tempMode, setTempMode] = useState(null);
  const showAlert =(message,type)=>{
    setAlert({
      msg: message,
      type: type
    })
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  }
  const togglenav=(tmode)=>{
    setMode(tmode);
  }
  const toggleMode=()=>{
    if(mode==='light'){
      // setMode('dark');
      togglenav('dark');
      setRedMode('dark');
      // setBlueMode('dark');
      setGreenMode('dark');
      document.body.style.backgroundColor='#000000';
      showAlert("Dark mode is enabled","success");
    }
    else{
      // setMode('light');
      togglenav('light'); 
      setRedMode('light');
      // setBlueMode('light');
      setGreenMode('light');
      setTempMode('light');
      document.body.style.backgroundColor='white';
      showAlert("Light mode is enabled","success");
    }
  }
  const toggleRed=()=>{
    if(redmode!=='danger'){
            setRedMode('danger');
            setTempMode(mode);
            togglenav('danger');
            document.body.style.backgroundColor='#ff0000';
            showAlert("Red mode is enabled","success");
          }
    else if(tempMode==='light'){
      setRedMode('light');
      togglenav('light');
      document.body.style.backgroundColor='#ffffff';
      showAlert("Light mode is re-enabled","success");
    }
    else{
      setRedMode('dark');
      togglenav('dark')
      document.body.style.backgroundColor='#000000';
      showAlert("Dark mode is re-enabled","success");
    }
  }
    const toggleGreen=()=>{
      if(greenmode!=='success'){
              setGreenMode('success');
              setTempMode(mode);
              togglenav('success');
              document.body.style.backgroundColor='#00FF00';
              showAlert("Green mode is enabled","success");
            }
      else if(tempMode==='light'){
        setGreenMode('light');
        togglenav('light');
        document.body.style.backgroundColor='white';
        showAlert("Light mode is re-enabled","success");
      }
      else{
        setGreenMode('dark');
        togglenav('dark'); 
        document.body.style.backgroundColor='#000000';
        showAlert("Dark mode is re-enabled","success");
      }
    }
      // const toggleBlue=()=>{
      //   if(bluemode!=='primary'){
      //           setBlueMode('primary');
      //           setTempMode(mode);
      //           document.body.style.backgroundColor='#0000ff';
      //           showAlert("Blue mode is enabled","success");
      //         }
      //   else if(tempMode==='light'){
      //     setBlueMode('light');
      //     document.body.style.backgroundColor='white';
      //     showAlert("Light mode is re-enabled","success");
      //   }
      //   else{
      //     setBlueMode('dark');
      //     document.body.style.backgroundColor='#000000';
      //     showAlert("Dark mode is re-enabled","success");
      //   }
      // }
    
  return (
    <>
    {/* <Router> */}
    <Navbar title="TextUtils" heading2="About" mode={mode} toggleMode={toggleMode}  redmode={redmode} toggleRed={toggleRed} greenmode={greenmode} toggleGreen={toggleGreen}/>
    <Alert alert={alert}/>
    <div className="container" my="3">
      <TextArea showAlert={showAlert} heading="Enter the text to be displayed to the users: " mode={mode}/>
    </div>
    {/* <Routes>
          <Route exact path="/about" element={<About/>}/>
          <Route exact path="/" element={}/>
        </Routes>
      </div>
    </Router> */}

    </>
  );
}
export default App;
